@import "../common/mixins";
@import "../common/variable";

.testimonials {
  padding-top: r(70);
  padding-bottom: r(75 + 95);
  &__title {
    color: $colorOrange;
  }
  &__swiper {
    position: relative;
    margin-top: r(70);
    .swiper {
      max-width: r(1103);
      margin-inline: auto;
    }
    .swiper-wrapper {}
    .swiper-slide {
      text-align: center;
    }
  }
  &__nav {
    button {
      position: absolute;
      top: 50%;
      -webkit-transform: translateY(-50%);
          -ms-transform: translateY(-50%);
              transform: translateY(-50%);
      display: -webkit-inline-box;
      display: -ms-inline-flexbox;
      display: inline-flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      -ms-flex-negative: 0;
          flex-shrink: 0;
      width: r(45);
      height: r(45);
      aspect-ratio: 1;
      color: $colorBrown;
      background-color: $colorYellow;
      border: none;
      border-radius: 50%;
      @include max-tablet {
        top: 100%;
        -webkit-transform: translateY(0);
            -ms-transform: translateY(0);
                transform: translateY(0);
      }
      &:hover {
        background-color: $colorOrange;
        color: $colorWhite;
      }
      svg {
        width: r(15);
        height: r(25);
      }
    }
  }
  &__prev {
    left: 0;
  }
  &__next {
    right: 0;
  }
  &__pagination {
    position: absolute;
    bottom: r(-95) !important;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    grid-gap: r(6);
    .swiper-pagination-bullet {
      all: unset;
      display: -webkit-inline-box;
      display: -ms-inline-flexbox;
      display: inline-flex;
      -ms-flex-negative: 0;
          flex-shrink: 0;
      margin: 0 !important;
      width: r(15);
      height: r(15);
      aspect-ratio: 1;
      cursor: pointer;
      border-radius: 50%;
      background-color: #F0F0F0;
      &.swiper-pagination-bullet-active {
        background-color: #D9D9D9;
      }
    }
  }
}